.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.payment__amount {
  min-width: 200px;
  border-radius: 99px;
  padding: 12px;
  margin: 5px;
  border: 1px solid rgb(57, 21, 87);
}
.payment__amount:focus {
  outline: none;
}
.payment__form {
  display: flex;
  flex-direction: column;
}
.payLater__button {
  background-color: rgb(194, 184, 44) !important;
  color: #fff !important;
  min-width: 300px;
  max-width: 300px;
  margin-top: 5px;
  border-radius: 99px;
  padding: 8px;
  margin-top: 10px;
  min-width: 200px;
  font-weight: 800;
  border: none;
}
.payLater__button:hover {
  background-color: rgb(182, 180, 69) !important;
  transition: 600ms linear;
}
@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.about {
  /* display: flex; */
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.about__right {
  padding: 9px;
}
.about__right h1 {
  padding-top: 20px;
  font-weight: 600;
  color: rgb(84, 175, 84);
}
.about__right p {
  color: rgb(77, 45, 102);
  font-size: medium;
  font-weight: 300;
}
.about__left {
  display: flex;
  align-items: center;
  justify-content: center;
}
.about__left img {
  max-width: 500px;
}
