.contact {
  background-color: rgb(240, 240, 240);
  padding-top: 30px;
  padding-bottom: 30px;
}
.contact h1 {
  color: rgb(57, 21, 87);
  font-weight: 600;
  font-size: 45px;
}
.contact p {
  color: rgb(57, 21, 87);
}
.contact h5 {
  color: rgb(57, 21, 87);
}
.contact__left form {
  display: flex;
  flex-direction: column;
  max-width: 500px;
}
.contact__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contact__left input,
.contact__left textarea {
  padding: 10px;
  margin: 10px;
  border-radius: 19px;
  border: 1px solid rgb(57, 21, 87);
  background-color: rgb(240, 240, 240);
}
.contact__left input:focus,
.contact__left textarea:focus {
  outline: none;
}
.contact__left .contact__button {
  background-color: rgb(45, 184, 45);
  color: #fff;
  border: none;
  font-size: 22px;
  font-weight: 700;
  max-width: 400px;
  border-radius: 999px;
}
.contact__left .contact__button:hover {
  background-color: rgb(62, 148, 62);
  transition: 600ms linear;
}
.contact__right iframe {
  border-radius: 19px;
  border: 1px solid rgb(57, 21, 87);
}
@media only screen and (max-width: 600px) {
  iframe {
    max-width: 300px;
  }
  .contact__right h5 {
    font-size: 16px;
  }
  .contact h1 {
    font-size: xx-large;
  }
}

