.landingPage {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  height: 80vh;
  background-image: url(/static/media/slide2.30d9d460.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
.landingPage__contents {
  display: flex;
  margin-top: 0px;
  padding: 15px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  color: rgb(255, 255, 255);
}
.landingPage__contents h3 {
  color: rgb(255, 255, 255);
  font-size: 40px;
  font-family: "Poppins", sans-serif;
}
.landingPage__contents h1 {
  font-family: "Poppins", sans-serif;

  color: rgb(250, 250, 250);
  font-size: 70px;
  letter-spacing: 3px;
  font-weight: 700;
}
.landingPage__contents button {
  color: #fff;
  background-color: rgb(84, 175, 84);
  border: none;
  min-height: 50px;
  min-width: 200px;
  margin: 0 !important;
  font-size: 21px;
  font-weight: 700;
  border-radius: 99px;
}
.landingPage__contents button:hover {
  background-color: rgb(66, 143, 66);
  transition: 600ms linear;
}
@media only screen and (max-width: 550px) {
  /* .landingPage {
    background-image: none;
    height: 50vh;
  } */
  .landingPage__contents {
    margin-top: 0px;
  }
  .landingPage__contents h3 {
    font-size: 13px;
  }
  .landingPage__contents h1 {
    font-size: 23px;
    letter-spacing: 0px;
  }
  .landingPage {
    min-height: 160px;
    object-fit: contain;
    height: 100%;
    width: 100vw;
    background-size: cover;
  }
  .landingPage__contents button {
    padding: 6px;
    min-height: 9px;
    min-width: 80px;
    margin: 0 !important;
    font-size: 10px;
    font-weight: 700;
    border-radius: 99px;
  }
}
@media screen and (max-width: 1000px) and (min-width: 551px) {
  .landingPage__contents {
    margin-top: 70px;
  }
  .landingPage__contents h3 {
    font-size: 28px;
  }
  .landingPage__contents h1 {
    font-size: 43px;
    letter-spacing: 0px;
  }
  .landingPage {
    min-height: 360px;
    object-fit: contain;
    height: 100%;
    width: 100vw;
    background-size: cover;
  }
  .landingPage__contents button {
    padding: 12px;
    min-height: 20px;
    min-width: 140px;
    margin: 0 !important;
    font-size: 14px;
    font-weight: 700;
    border-radius: 99px;
  }
}

