.navbar {
  /* background-color: transparent; */
  height: 90px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0rem;
  position: absolute;
  top: 0;
  z-index: 999;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90px;
  width: 100vw;
}

.nav-logo {
  justify-self: start;
  margin-left: 20px;
