  display: flex;
  align-items: center;
  justify-content: center;
}
.logo {
  width: 90px;
  height: 50px;
}

.nav-menu {
  display: flex;
  /* grid-template-columns: repeat(7, auto); */
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  padding-left: 50px;
}

.nav-item {
  height: 50px;
  z-index: 1;
  font-size: 12px;
}

.nav-links {
  color: #3c1c56;
  font-size: 17px;
  font-weight: 700;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1.2rem;
  height: 100%;
}

.nav-links:hover {
  color: rgb(50, 122, 62);
  border-bottom: 4px solid rgb(42, 107, 47);
  transition: all 0.2s ease-out;
}

.fa-phone {
  font-size: 18px;
}
.fa-bars {
  color: rgb(57, 21, 87);
}
.fa-user {
  color: rgb(57, 21, 87);
}
.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}
@media screen and (max-width: 960px) {
  .nav {
    padding: 0;
  }
  .navbar {
    background-color: #6f6b7e;
  }
  .nav-menu {
    margin-top: -100px;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 23%;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #dad9db;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    padding: 46px 0px;
  }

  .nav-links {
    text-align: center;
    padding: 1rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    margin-top: -25px;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: rgb(57, 21, 87);
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    /* background: #fff; */
    color: #242424;
    transition: 250ms;
  }
  .head-img {
    width: 95%;
  }
  .home-container {
    width: 100%;
    margin: 0;
    align-items: center;
    line-height: 2;
    max-width: none;
  }
  .main-head {
    text-align: center;
  }

  .navMobile {
    display: flex;
    width: 100vw;
    align-items: center;
    justify-content: space-between;
  }
}
@media only screen and (max-width: 600px) {
